  }

  #logo {
    font-size: 1.5rem;
  }

  .nav-links li {
    padding-left: 5rem;
    font-size: 1.2rem;
  }

  .about-title {
    left: 10%;
  }

  .about-paragraph {
    left: 10%;
    height: 25vh;
  }

  .profile-pic {
    left: 55%;
  }

  #experiences {
    margin-top: -25rem;
  }

  .header {
    top: 5%;
  }

  .timeline {
    top: 22%;
  }

  .experience-card {
    top: 210%;
    width: 50vw;
  }

  #projects {
    margin-top: -20rem;
  }

  .project {
    top: 15%;
  }

  #contact {
    margin-top: -10rem;
    height: 80vh;
  }
}

@media (max-width:961px)  {
  .title-text {
    font-size: 4rem;
  }

  #logo {
    font-size: 1.2rem;
  }

  .nav-links li {
    padding-left: 3rem;
    font-size: 1rem;
  }

  .about-title {
    left: 0%;
  }

  .about-paragraph {
    left: 0%;
    height: 35vh;
    padding-right: 5%;
  }

  .profile-pic {
    left: 55%;
    width: 35%;
  }

  #experiences {
    margin-top: -10rem;
  }

  .timeline {
    top: 25%;
  }

  .experience-card {
    top: 230%;
    width: 65vw;
  }

  #projects {
    margin-top: 0rem;
  }

  .project {
    top: 5%;
  }

  #contact {
    margin-top: 0rem;
    height: 90vh;
  }
}


@media (max-width:641px) {
  .title-text {
    font-size: 3rem;
    width: 15rem;
    text-align: center;
  }

  .landing {
    height: 75vh;
    background-size: 100% 75%;
  }

  .nav-bar {
    display: none;
  }

  .about-title {
    left: 0%;
    font-size: 35px;
  }

  .about-paragraph {
    left: -14%;
    height: 45vh;
    width: 75vw;
  }

  .profile-pic {
    left: 25%;
    width: 50%;
    top: 133%;
  }

  #experiences {
    margin-top: 0rem;
  }

  .header {
    font-size: 35px;
    top: 10%;
  }

  .timeline {
    top: 35%;
    width: 85%;
  }

  .experience-card {
    top: 235%;
    width: 85vw;
  }

  .experience-desc {
    min-height: 33vh;
  }

  .company-image {
    text-align: center;
  }

  .round {
    width: 100px;
  }

  #carousel {
    top: 65%;
  }

  .nextPrev {
    top: 50%;
  }

  .contacts-list {
    left: -12px;
  }

  .icons {
    font-size: 3rem;
  }

  .linkedin {
    width: 46px;
    margin-top: -12px;
  }

  .mail {
    width: 43px;
    margin-top: -11px;
    margin-left: 5px;
  }

  .gif {
    width: 15rem;
  }
}

@media (max-width:481px)  {
  .about-title {
    left: 4%;
    font-size: 35px;
  }

  .about-paragraph {
    left: -12%;
    height: 45vh;
    width: 75vw;
  }

  .contacts-list {
    left: -3.5px;
  }
}

@media (min-width:320px)  { /* smartphones, iPhone, portrait 480x320 phones */ }
